.SectionContainer{
    background-color: #0C0032;
    padding-top: 7rem;
    padding-bottom: 2rem;
    display: flex;
    flex-direction: column;
    align-items: center;
}


.IntroductionContainer{
    width: 70%;
    /* margin: auto; */
    display: flex;
    flex-direction: row;
    padding-right: 3rem;
}

.imgContainer{
    display: flex;
    align-items: center;
    justify-content: center;
}

.imgContainer img{
    height: 75%;
    
}

.TextHeader{
    font-family: "Oswald";
    font-size: 6rem;
    font-weight: 400;
    margin: 0;
    text-shadow: 2px 2px 5px red;
    color: white;    
}

.TextBody{
    font-family: "Josefin Sans";
    text-align: center;
    font-size: 1.5rem;
    font-weight: 500;
    color: wheat;

}

.TextContainer{
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;


}

.popupText{
    font-family: "Oswald";
    color: white;
    font-size: 8em;
    font-weight: 400;
    text-shadow: 2px 2px 5px red;
    text-align: center;
    margin: 0;
    padding: 0;
    transform: translateY(0px);
    opacity: 1;
    transition: all 2s ease;

}

.popupFalse{
    font-family: "Oswald";
    color: white;
    font-size: 8em;
    font-weight: 400;
    text-shadow: 2px 2px 5px red;
    text-align: center;
    margin: 0;
    padding: 0;
    transform: translateY(50px);
    opacity: 0;

}

.rankContainer{
    display: flex;
    justify-content: center;
    
}

.rankLogoContainer{
    position: relative;
}

.rankContainer span{
    font-size: 3rem;
    color: white;
    position: absolute;
    bottom: 1rem;   
    left: 0;
    right: 0;
}

.rankContainer h1 {
    font-size: 1.3rem;
    color: white;
    font-weight: 300;
    margin: 0;
}


@media screen and (max-width: 1500px) {
    .rankLogoContainer img{
        width: 70%;
    }
    
    .TextHeader{
        font-size: 4rem;
    }

    .TextBody{
        font-size: 1.3rem;
    }
    
    .rankContainer span{
        font-size: 2rem;
        bottom: 1rem;
    }
}


@media screen and (max-width: 1000px) {
    .SectionContainer{
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }

    .IntroductionContainer{
        flex-direction: column;
        justify-content: center;
        align-items: center;
        padding: 0;
        width: 80%;
    }

    .TextHeader{
        
    }

    .SectionContainer{
        padding-top: 2rem;
    }

    .rankContainer img{
        width: 40%;
    }

    .rankContainer span{
        font-size: 1.3rem;
        bottom: 0.5rem;
    }

}

@media screen and (max-width: 800px) {
    .TextHeader{
        font-size: 3.5rem;
        
    }

    .TextBody{
        font-size: 0.8rem;
    }
}